import type { Employee } from "@/lib/mock-data"
import { Trophy, Medal, Award } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"

interface LeaderboardTableProps {
  employees: Employee[]
  title: string
}

export function LeaderboardTable({ employees, title }: LeaderboardTableProps) {
  const getRankIcon = (rank: number) => {
    if (rank === 1) return <Trophy className="w-4 h-4 md:w-5 md:h-5 lg:w-6 lg:h-6 text-[#FFB100]" />
    if (rank === 2) return <Medal className="w-4 h-4 md:w-5 md:h-5 lg:w-6 lg:h-6 text-[#004EFF]" />
    if (rank === 3) return <Award className="w-4 h-4 md:w-5 md:h-5 lg:w-6 lg:h-6 text-black" />
    return null
  }

  const getRankColor = (rank: number) => {
    if (rank === 1) return "bg-[#FFB100]/5 border-l-4 border-[#FFB100]"
    if (rank === 2) return "bg-[#004EFF]/5 border-l-4 border-[#004EFF]"
    if (rank === 3) return "bg-gray-100/50 border-l-4 border-gray-800"
    return ""
  }

  return (
    <div className="h-full w-full flex flex-col p-2 md:p-3 lg:p-4 bg-gray-50 overflow-hidden">
      <div className="max-w-7xl mx-auto w-full px-2 md:px-4 flex flex-col h-full">
        <CardHeader className="text-center pb-2 md:pb-3 lg:pb-4 flex-shrink-0">
          <CardTitle className="text-2xl md:text-3xl lg:text-4xl font-bold text-gray-900">{title}</CardTitle>
        </CardHeader>

        <Card className="overflow-hidden flex-1 flex flex-col min-h-0">
          <CardContent className="p-0 flex-1 flex flex-col overflow-hidden">
            <div className="flex-1 overflow-y-auto">
              {employees.map((employee, index) => (
                <div key={employee.id}>
                  <div
                    className={`flex items-center gap-2 md:gap-3 p-2 md:p-3 lg:p-4 transition-all duration-200 animate-in fade-in slide-in-from-left hover:bg-gray-50 ${getRankColor(index + 1)}`}
                    style={{ animationDelay: `${index * 30}ms` }}
                  >
                    <div className="flex items-center gap-1.5 md:gap-2 min-w-[70px] md:min-w-[80px] lg:min-w-[100px]">
                      <span className="text-base md:text-lg lg:text-xl font-bold text-gray-400 w-5 md:w-6 lg:w-8 text-right">{index + 1}</span>
                      <div className="w-4 h-4 md:w-5 md:h-5 lg:w-6 lg:h-6 flex items-center justify-center">
                        {getRankIcon(index + 1)}
                      </div>
                    </div>

                    <Avatar className="w-8 h-8 md:w-10 md:h-10 lg:w-12 lg:h-12 bg-[#004EFF] shadow-sm flex-shrink-0">
                      <AvatarFallback className="text-xs md:text-sm lg:text-base font-bold text-white bg-[#004EFF]">
                        {employee.avatar}
                      </AvatarFallback>
                    </Avatar>

                    <div className="flex-1 min-w-0">
                      <h3 className="text-sm md:text-base lg:text-lg font-bold text-gray-900 leading-tight truncate">{employee.name}</h3>
                      <p className="text-xs md:text-sm text-gray-500 truncate">{employee.department}</p>
                    </div>

                    <div className="text-right flex-shrink-0">
                      <div className="text-lg md:text-xl lg:text-2xl font-bold text-gray-900">{employee.dvdScore.toLocaleString()}</div>
                      <p className="text-xs text-gray-500">DVD Score</p>
                    </div>
                  </div>
                  {index < employees.length - 1 && <Separator />}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
